html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

html {
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}

body {
  background-color: #000;
  font-family: DM Sans, serif;
  position: relative;
}

.container {
  max-width: 1246px;
  margin: 0 auto;
  padding: 0 15px;
  transition: all .5s;
}

@media screen and (width >= 362px) {
  .container {
    width: 362px;
  }
}

@media screen and (width >= 728px) {
  .container {
    width: 728px;
  }
}

@media screen and (width >= 1246px) {
  .container {
    width: 1246px;
  }
}

.header {
  padding: 15px 0 17px;
}

.header .container {
  background: #ffffffe6;
  border-radius: 75px;
  padding-top: 15px;
  padding-bottom: 17px;
}
